import React, {Component} from 'react';
import PropTypes from 'prop-types';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class KvideoComp extends Component {

    constructor(props) {
        super(props);
        this.pc = null;
        this.clickCount = 0;
    }
    
    negotiate() {
        const pc = this.pc;
        pc.addTransceiver('video', {direction: 'recvonly'});
        return pc.createOffer().then(function(offer) {
            return pc.setLocalDescription(offer);
        }).then(function() {
            // wait for ICE gathering to complete
            return new Promise(function(resolve) {
                if (pc.iceGatheringState === 'complete') {
                    resolve();
                } else {
                    function checkState() {
                        if (pc.iceGatheringState === 'complete') {
                            pc.removeEventListener('icegatheringstatechange', checkState);
                            resolve();
                        }
                    }
                    pc.addEventListener('icegatheringstatechange', checkState);
                }
            });
        }).then(function() {
            var offer = pc.localDescription;
            return fetch('/offer', {
                body: JSON.stringify({
                    sdp: offer.sdp,
                    type: offer.type,
                }),
                headers: {
                    'Content-Type': 'application/json'
                },
                method: 'POST'
            });
        }).then(function(response) {
            return response.json();
        }).then(function(answer) {
            return pc.setRemoteDescription(answer);
        });
    }

    start() {
        const {id} = this.props;        
        const config = {
            sdpSemantics: 'unified-plan'
        };
        config.iceServers = [{urls: ['stun:stun.l.google.com:19302']}];
        this.pc = new RTCPeerConnection(config);

        // connect video
        this.pc.addEventListener('track', function(evt) {
            if (evt.track.kind == 'video') {
                document.getElementById(id).srcObject = evt.streams[0];
            } 
        });

        this.negotiate();
    }

    stop() {
        if (this.pc!==null)
            this.pc.close();
    }

    componentDidMount() {
        const {id, overlay_id} = this.props;
        const video = document.getElementById(id);
        const handlePointer = (e) => {
            if (e.button==0) {
                const {source_width, source_height} = this.props;
                const x = Math.floor(e.offsetX/video.clientWidth*source_width);
                const y = Math.floor(e.offsetY/video.clientHeight*source_height);
                this.props.setProps({click_data: [this.clickCount, x, y]});
                this.clickCount++;
            }
        }
        const handleTimer = () => {
            const figure = document.getElementById(overlay_id);
            const nsew = document.getElementsByClassName("nsewdrag")[0];
            if (figure)
                figure.addEventListener("pointerdown", handlePointer);
            // Change cursor to crosshair for nsewdrag
            if (nsew)
                nsew.style.cursor = "crosshair";

        }
        video.addEventListener("pointerdown", handlePointer);
        // We need to wait a bit for page initialization before we can add event listener, etc.
        setTimeout(handleTimer, 1000);
        this.start();
    }

    componentWillUnmount() {
        this.stop();
    }

    render() {
        const {id, style} = this.props;
        return (
            <video id={id} autoPlay playsInline muted style={style}></video>
         );
    }
}

KvideoComp.defaultProps = {};

KvideoComp.propTypes = {

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The ID used to identify an overlay component that we register mouse events.
     */
    overlay_id: PropTypes.string,

    /**
     * click_data 
     */
    click_data: PropTypes.array,

    /**
     * source width, hint for click callback.
     */
    source_width: PropTypes.number,

    /**
     * source height, hint for click callback.
     */
    source_height: PropTypes.number,

    /**
     * style is used to pass style parameters to video component.
     */
    style: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
